.vetskitchen-calorie-calculator {
  position: relative;
  padding: 50px 72px 170px;
  margin-bottom: 50px;
  background: #98debe url("../../assets/images/calculator/bg-img.webp") no-repeat bottom;
  background-size: 100% auto;
  border-radius: 40px;
}
.vetskitchen-calorie-calculator .custom-row {
  display: flex;
  justify-content: space-between;
}
.vetskitchen-calorie-calculator .custom-row .cols-4 {
  width: 43%;
}
.vetskitchen-calorie-calculator .custom-row .cols-8 {
  width: 80%;
}
.vetskitchen-calorie-calculator .custom-row .cols-5 {
  width: 50%;
}
.vetskitchen-calorie-calculator .custom-row .cols-6 {
  width: 60%;
}
.vetskitchen-calorie-calculator .blue-box {
  padding: 5px;
  background-color: #2fa5ec;
  border-top-left-radius: 20px;
  border-bottom-right-radius: 20px;
}
.vetskitchen-calorie-calculator .blue-box .inner-wrapper {
  padding: 25px 25px 35px;
  border: 2px dashed #ffffff;
  border-top-left-radius: 20px;
  border-bottom-right-radius: 20px;
}
.vetskitchen-calorie-calculator .blue-box h3 {
  font-size: 26px;
  color: #ffffff;
  text-align: center;
}
.vetskitchen-calorie-calculator .blue-box .wrapper {
  display: flex;
  margin-bottom: 30px;
  font-size: 14px;
  color: #ffffff;
}
.vetskitchen-calorie-calculator .blue-box .wrapper:last-of-type {
  margin-bottom: 0;
}
.vetskitchen-calorie-calculator .blue-box .wrapper h4 {
  margin-bottom: 0;
  font-size: 15px;
  color: #ffffff;
}
.vetskitchen-calorie-calculator .blue-box .img-box {
  margin-right: 15px;
  flex: 0 0 15%;
}
.vetskitchen-calorie-calculator .blue-box .text-block > *:last-child {
  margin-bottom: 0;
}
.vetskitchen-calorie-calculator .item .content {
  position: relative;
  padding-left: 80px;
  padding-bottom: 30px;
}
.vetskitchen-calorie-calculator .item .item-count {
  position: absolute;
  top: 0;
  left: -15px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
  font-family: "Signika", sans-serif;
  font-size: 18px;
  color: #198077;
  background-color: #ffffff;
  border-radius: 100%;
}
.vetskitchen-calorie-calculator .form-group {
  margin-bottom: 30px;
}
.vetskitchen-calorie-calculator label {
  display: block;
  margin-bottom: 5px;
  font-family: "Signika", sans-serif;
  color: #105e58;
  font-size: 16px;
}
.vetskitchen-calorie-calculator .cross_breed_confimation_box {
  display: none;
  padding: 15px;
  margin-top: 20px;
  min-height: 175px;
  font-size: 12px;
  color: #707070;
  text-align: center;
  background-color: #fff2eb;
  border-radius: 20px;
}
.vetskitchen-calorie-calculator .cross_breed_confimation_box h4 {
  font-family: "Signika", sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.2;
  color: #707070;
}
.vetskitchen-calorie-calculator .cross_breed_confimation_box .buttons .custom-radio label {
  color: #ffffff;
  background-color: #858585;
}
.vetskitchen-calorie-calculator .buttons .custom-radio {
  position: relative;
  display: inline-block;
  margin-right: 28px;
  width: auto;
}
.vetskitchen-calorie-calculator .buttons .custom-radio input {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
}
.vetskitchen-calorie-calculator .buttons .custom-radio input:checked ~ label {
  color: #ffffff;
  background-color: #fc8f25;
}
.vetskitchen-calorie-calculator .buttons .custom-radio label {
  padding: 9px 22px;
  margin: 0;
  font-family: "Hind", sans-serif;
  font-size: 14px;
  color: #858585;
  background-color: #ffffff;
  border-radius: 5px;
}
.vetskitchen-calorie-calculator .form-input {
  min-height: 40px;
  border-radius: 5px;
}
.vetskitchen-calorie-calculator .form-input.w-45 {
  width: 45%;
}
.vetskitchen-calorie-calculator .form-input.w-20 {
  width: 20%;
}
.vetskitchen-calorie-calculator select.form-input {
  min-height: 40px;
  border-radius: 5px;
  background: #f2f4f5 url("../../assets/icons/down-arrow.svg") no-repeat center right 15px;
  background-size: 15px;
}
.vetskitchen-calorie-calculator select#dog_weight_type {
  padding: 10px;
  background-position: center right 8px;
  background-size: 8px;
}
.vetskitchen-calorie-calculator .form-inline {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.vetskitchen-calorie-calculator .form-inline.justify-close {
  justify-content: unset;
  gap: 35px;
}
.vetskitchen-calorie-calculator .fully_grown_no_box {
  display: none;
  padding: 20px;
  margin-bottom: 10px;
  text-align: center;
  background-color: #ffffff;
  border-radius: 20px;
}
.vetskitchen-calorie-calculator .fully_grown_no_box h4 {
  margin-bottom: 0;
  font-size: 20px;
  color: #707070;
}
.vetskitchen-calorie-calculator .steps_btn {
  display: inline-block;
  padding: 5px 26px;
  font-family: "Signika", sans-serif;
  font-size: 12px;
  color: #ffffff;
  text-decoration: none;
  background-color: #fc8f25;
  border: 1px solid #fc8f25;
  border-radius: 6px;
}
.vetskitchen-calorie-calculator .steps_btn:hover {
  color: #fc8f25;
  background-color: #ffffff;
}
.vetskitchen-calorie-calculator .button {
  text-align: right;
}
.vetskitchen-calorie-calculator .button input {
  display: inline-block;
  padding: 10px 46px;
  margin-top: 30px;
  font-family: "Signika";
  font-size: 21px;
  color: #ffffff;
  background-color: #fc8f25;
  border: 1px solid #fc8f25;
  border-radius: 6px;
  transition: 0.2s all ease-in-out;
}
.vetskitchen-calorie-calculator .button input:hover {
  color: #fc8f25;
  background-color: #ffffff;
}
.vetskitchen-calorie-calculator .select-weigh .custom-box {
  display: flex;
  flex-wrap: wrap;
  margin-left: -25px;
  margin-right: -25px;
}
.vetskitchen-calorie-calculator .select-weigh .cols {
  padding: 0 25px;
  width: 33.3333%;
}
.vetskitchen-calorie-calculator .select-weigh .boxes {
  position: relative;
  margin-bottom: 30px;
}
.vetskitchen-calorie-calculator .select-weigh .boxes input {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
}
.vetskitchen-calorie-calculator .select-weigh .boxes input:checked ~ .inner-box .img-box .selected-img {
  display: block;
}
.vetskitchen-calorie-calculator .select-weigh .boxes input:checked ~ .inner-box .img-box .show-img {
  display: none;
}
.vetskitchen-calorie-calculator .select-weigh .boxes input:checked ~ .inner-box .inner-count {
  color: #ffffff;
  background-color: #fc8f25;
}
.vetskitchen-calorie-calculator .select-weigh .boxes .img-box {
  margin: auto;
  width: 80%;
}
.vetskitchen-calorie-calculator .select-weigh .boxes .img-box .selected-img {
  display: none;
}
.vetskitchen-calorie-calculator .select-weigh .inner-count {
  position: absolute;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 3px;
  width: 35px;
  height: 35px;
  font-family: Signika;
  font-size: 18px;
  color: #198077;
  background-color: #ffffff;
  border: 2px solid #ffffff;
  border-radius: 100%;
}
.vetskitchen-calorie-calculator .select-weigh .inner-box {
  position: relative;
}
.vetskitchen-calorie-calculator .select-weigh .text-box {
  padding: 15px;
  margin-top: 20px;
  min-height: 175px;
  font-size: 12px;
  color: #707070;
  text-align: center;
  background-color: #fff2eb;
  border-radius: 20px;
}
.vetskitchen-calorie-calculator .select-weigh .text-box h4 {
  margin-bottom: 10px;
  font-size: 16px;
  font-weight: bolder;
  line-height: 1.2;
  color: #707070;
}
.vetskitchen-calorie-calculator .select-weigh .text-box > *:last-child {
  margin-bottom: 0;
}
.vetskitchen-calorie-calculator .suggested_ideal_weight_wrapper {
  display: none;
}
.vetskitchen-calorie-calculator .disclaimer-box {
  padding: 10px 20px 20px;
  font-size: 14px;
  line-height: 1.2;
  text-align: center;
  background-color: #ffffff;
  border-radius: 20px;
}
.vetskitchen-calorie-calculator .disclaimer-box h4 {
  margin: 0;
  font-size: 20px;
  color: #707070;
}
.vetskitchen-calorie-calculator .disclaimer-box > *:last-child {
  margin-bottom: 0;
}
.vetskitchen-calorie-calculator .border-left {
  border-left: 1px solid #dee2e6;
}
.vetskitchen-calorie-calculator .results_block {
  display: none;
  padding: 20px;
  margin: 15px 0 0;
  text-align: center;
  background-color: #ffffff;
  border-radius: 20px;
}
.vetskitchen-calorie-calculator .results_block > div {
  flex: 1;
  font-size: 14px;
  color: #105e58;
}
.vetskitchen-calorie-calculator .results_block span {
  display: block;
  color: #fc8f25;
  font-weight: 900;
  font-size: 46px;
  line-height: 1.2;
}
.vetskitchen-calorie-calculator .results_block h5 {
  margin-bottom: 0;
  font-size: 17px;
  font-weight: 400;
  color: #4e756e;
}
.vetskitchen-calorie-calculator .results_block b {
  color: #fc8f25;
  font-weight: 900;
}
.vetskitchen-calorie-calculator ~ .product_box_wrapper .product_box_title h2 {
  padding: 30px 0;
  font-size: 35px;
  color: #40a099;
  text-align: center;
  text-transform: capitalize;
}
.vetskitchen-calorie-calculator ~ .product_box_wrapper .products_wrapper {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 30px 0;
  max-width: 90%;
}
@media screen and (max-width: 1024px) {
  .vetskitchen-calorie-calculator ~ .product_box_wrapper .products_wrapper {
    max-width: 100%;
  }
}
.vetskitchen-calorie-calculator ~ .product_box_wrapper .product_box_left {
  width: 75%;
}
.vetskitchen-calorie-calculator ~ .product_box_wrapper .product_box_left_wrapper {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  align-items: center;
}
.vetskitchen-calorie-calculator ~ .product_box_wrapper .product_box_left_wrapper .product_img {
  width: 20%;
}
.vetskitchen-calorie-calculator ~ .product_box_wrapper .product_box_left_wrapper .product_img img {
  width: 100%;
}
.vetskitchen-calorie-calculator ~ .product_box_wrapper .product_box_left_wrapper .product_desc {
  padding-left: 30px;
  width: 80%;
  max-width: 70%;
  font-size: 18px;
  color: #4c4d4d;
}
.vetskitchen-calorie-calculator ~ .product_box_wrapper .product_box_left_wrapper .product_desc h3 {
  margin-bottom: 0;
  font-size: 24px;
  line-height: 1.2;
}
.vetskitchen-calorie-calculator ~ .product_box_wrapper .product_box_left_wrapper .product_desc .price {
  font-size: 24px;
  font-weight: bold;
  color: #40a099;
}
.vetskitchen-calorie-calculator ~ .product_box_wrapper .product_box_left_wrapper .product_desc button {
  padding: 10px 18px;
  font-size: 17px;
  font-weight: bold;
  color: #ffffff;
  text-transform: uppercase;
  background: #40a099;
  border: none;
  outline: none;
  border-radius: 6px;
}
.vetskitchen-calorie-calculator ~ .product_box_wrapper .product_box_left_wrapper .product_desc a {
  padding-left: 50px;
  font-size: 20px;
  font-weight: 700;
  color: #fc8f25;
}
.vetskitchen-calorie-calculator ~ .product_box_wrapper .product_box_left_wrapper .product_desc a img {
  padding-left: 15px;
  height: 23px;
  text-align: left;
  object-fit: contain;
}
.vetskitchen-calorie-calculator ~ .product_box_wrapper .product_box_right {
  width: 25%;
  text-align: center;
}
.vetskitchen-calorie-calculator ~ .product_box_wrapper .product_box_right h2, .vetskitchen-calorie-calculator ~ .product_box_wrapper .product_box_right h4, .vetskitchen-calorie-calculator ~ .product_box_wrapper .product_box_right h5 {
  color: #575757;
}
.vetskitchen-calorie-calculator ~ .product_box_wrapper .product_box_right h2 {
  margin-bottom: 0;
  font-size: 30px;
}
.vetskitchen-calorie-calculator ~ .product_box_wrapper .product_box_right h4 {
  font-size: 18px;
}
.vetskitchen-calorie-calculator ~ .product_box_wrapper .product_box_right h5 {
  font-size: 17px;
}
.vetskitchen-calorie-calculator ~ .product_box_wrapper .product_box_right img {
  width: 110px;
  height: 90px;
  object-fit: contain;
  vertical-align: top;
}
.vetskitchen-calorie-calculator ~ .product_box_wrapper .product_box_main:nth-child(odd) {
  background-color: #f2f2f2;
}
@media screen and (max-width: 767px) {
  .vetskitchen-calorie-calculator ~ .product_box_wrapper .product_box_left {
    width: 100%;
  }
  .vetskitchen-calorie-calculator ~ .product_box_wrapper .product_box_left_wrapper .product_img {
    margin-bottom: 15px;
    width: 100%;
  }
  .vetskitchen-calorie-calculator ~ .product_box_wrapper .product_box_left_wrapper .product_desc {
    padding-left: 0;
    width: 100%;
    max-width: 100%;
  }
  .vetskitchen-calorie-calculator ~ .product_box_wrapper .product_box_right {
    margin-top: 25px;
    width: 100%;
  }
}
@media screen and (max-width: 1024px) {
  .vetskitchen-calorie-calculator .custom-row {
    display: block;
  }
  .vetskitchen-calorie-calculator .custom-row .cols-4,
  .vetskitchen-calorie-calculator .custom-row .cols-8,
  .vetskitchen-calorie-calculator .custom-row .cols-5,
  .vetskitchen-calorie-calculator .custom-row .cols-6 {
    width: 100%;
  }
  .vetskitchen-calorie-calculator .blue-box {
    margin-bottom: 50px;
  }
  .vetskitchen-calorie-calculator .select-weigh .cols {
    padding: 0 15px;
    width: 50%;
  }
  .vetskitchen-calorie-calculator .product_box_main .container {
    max-width: 920px;
  }
}
@media screen and (max-width: 991px) {
  .vetskitchen-calorie-calculator {
    padding: 20px 20px 80px;
    border-radius: 20px;
  }
  .vetskitchen-calorie-calculator .select-weigh .cols {
    width: 100%;
    max-width: 280px;
  }
  .vetskitchen-calorie-calculator .item .content {
    padding-left: 20px;
    padding-bottom: 30px;
  }
}
@media screen and (max-width: 767px) {
  .vetskitchen-calorie-calculator .results_block {
    flex-direction: column;
  }
}